import React, { useEffect, useContext, useState } from "react";
import Colorpicker from "./Colorpicker";
import { useLocation, useHistory, Link } from "react-router-dom";
import { ItemsContext } from "../App";

export default function EditItem() {
  const location = useLocation();
  const history = useHistory();

  const { items, setItems } = useContext(ItemsContext);
  const item = items.find(
    ({ id }) => id === +location.pathname.replace("/edit/", "")
  );
  const [name, setName] = useState(item ? item.name : "");
  const [type, setType] = useState(item ? item.type : "");
  const [color, setColor] = useState(
    item ? item.color : { hex: "#aabbcc", rgb: [], srgb: [] }
  );

  const changeItems = () =>
    item
      ? changeItem({
          name,
          type,
          color,
          id: item.id,
        })
      : addItem({ name, type, color, id: items[items.length - 1].id + 1 });

  const hexToRgb = (hex) => {
    const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
    return result
      ? [
          parseInt(result[1], 16),
          parseInt(result[2], 16),
          parseInt(result[3], 16),
        ]
      : null;
  };

  const changeColor = (data = color.hex) => {
    setColor({
      hex: data,
      rgb: hexToRgb(data),
      srgb: hexToRgb(data),
    });
  };

  useEffect(changeColor, [color.hex]);

  const addItem = (newItem) => {
    setItems([...items, newItem]);
    history.push("/");
  };

  const changeItem = (newItem) => {
    const arr = [...items];
    arr[items.findIndex(({ id }) => id === newItem.id)] = newItem;
    setItems(arr);
    history.push("/");
  };

  const changeName = (event) => setName(event.target.value);
  const changeType = (event) => setType(event.target.value);

  return (
    <div className="changing-form justify-content-center">
      <div className="mt-3 col-8 ">
        <label htmlFor="exampleFormControlInput1" className="form-label">
          Name
        </label>
        <input
          value={name}
          onChange={changeName}
          type="text"
          className="form-control"
          placeholder="name"
        />
      </div>
      <div className="mt-3 mb-3 col-8">
        <label htmlFor="exampleFormControlInput1" className="form-label">
          Type
        </label>
        <input
          type="text"
          value={type}
          onChange={changeType}
          className="form-control"
          placeholder="type"
        />
      </div>
      <Colorpicker color={color} setColor={setColor} />
      <div className="buttons mt-5">
        <Link to="/" className="btn btn-outline-secondary">
          Back
        </Link>
        <button onClick={changeItems} className="btn btn-outline-primary">
          Submit
        </button>
      </div>
    </div>
  );
}
