import React, { createContext, useContext } from "react";
import { Link } from "react-router-dom";
import { ItemsContext } from "../App";
import ColorValues from "./ColorValues";

export const Data = createContext({});

export default function ItemCart({ item }) {
  const { items, setItems } = useContext(ItemsContext);

  const deleteItem = () => setItems(items.filter(({ id }) => id !== item.id));
  return (
    <div className="item mt-3 col-xl-5 col-l-8">
      <div className="item-cart row">
        <div className="item-cart__titles titles col-12 col-sm-6">
          <h5 className="titles__name">
            <span>Name:</span> {item.name}
          </h5>
          <h5 className="titles__name">
            <span>Type:</span> {item.type}
          </h5>
        </div>
        <ColorValues
          className="item-cart__colors col-12 col-sm-6"
          color={item.color}
        />
      </div>
      <div className="buttons mt-2">
        <Link
          to={"/edit/" + item.id}
          type="button"
          className="btn btn-outline-primary"
        >
          Edit
        </Link>
        <button
          onClick={deleteItem}
          type="button"
          className="btn btn-outline-danger "
        >
          Delete
        </button>
      </div>
    </div>
  );
}
