import React, { useContext, useEffect } from "react";
import { HexColorPicker } from "react-colorful";
import ColorValues from "./ColorValues";

export default function Colorpicker({ color, setColor }) {
  const changeColor = (hex) => setColor({ ...color, hex });
  return (
    <div className="color-picker row col-12 justify-content-center">
      <HexColorPicker color={color.hex} onChange={changeColor} />
      <ColorValues
        className="color-picker__colors col-12 col-lg-6 justify-content-center"
        color={color}
        setColor={setColor}
      />
    </div>
  );
}
