import React, { useEffect } from "react";

export default function ColorValues({ className, color }) {
  return (
    <div className={className + " colors"}>
      {Object.keys(color).map((key) => (
        <div className="color-block" key={key}>
          <h4 className="color-block__title">{key}:</h4>
          <div className="color-block__content">
            <div
              className="color-block__color"
              style={{ backgroundColor: color.hex }}
            ></div>
            <div className="color-block__value">
              {key === "hex" ? color[key] : color[key].join(", ")}
            </div>
          </div>
        </div>
      ))}
    </div>
  );
}
